import { CST } from '../CST';
import { start } from 'repl';

export class MenuScene extends Phaser.Scene {
    constructor() {
        super({key: CST.SCENES.MENU})
    }

    init(data){
        console.log(data);
        
    }

    create(){

        let image = this.add.image(0,0, CST.SPRITES.BG.MENU_BG.SKY).setOrigin(0).setDepth(0);
        image.displayHeight = 600;
        image.displayWidth = 800;

        let hoverImage = this.add.image(0, 0, CST.SPRITES.UI.ICONS).setDepth(2);
        hoverImage.setVisible(false);
        this.add.text(this.game.renderer.width/4, this.game.renderer.height * 0.20, "CRAZY TANKS", {fontFamily: "Arial", fontSize: "64px", backgroundColor: "#000"}).setDepth(1);

        let startButton =  this.add.image(this.game.renderer.width/2, this.game.renderer.height/2, CST.SPRITES.UI.START);
        startButton.setScale(.5);
        startButton.setInteractive();
        startButton.on('pointerover', () => {
            hoverImage.setVisible(true);
            hoverImage.x = startButton.x - startButton.width/2;
            hoverImage.y = startButton.y;
            
        });
        startButton.on('pointerout', () => {
            hoverImage.setVisible(false);
        });
    }
}