import { CST } from '../CST';

export class LoadScene extends Phaser.Scene {
    constructor() {
        super({key: CST.SCENES.LOAD})
    }

    preload(){
        this.load.audio(CST.SOUNDS.FX.CONFIRM, "./assets/Sounds/fx/confirm.mp3");
        this.load.audio(CST.SOUNDS.BGM.MENU_THEME, "./assets/Sounds/bgm/menu_theme.mp3");

        this.load.image(CST.SPRITES.BG.MENU_BG.SKY, "./assets/Sprites/bg/bg.png");
        this.load.image(CST.SPRITES.BG.MENU_BG.TREE, "./assets/Sprites/bg/trees.png")
        this.load.image(CST.SPRITES.BG.MENU_BG.MOUNTAIN, "./assets/Sprites/bg/mountain.png")
        this.load.image(CST.SPRITES.BG.MENU_BG.MOUNTAIN_TREE, "./assets/Sprites/bg/mountain-trees.png")
        this.load.image(CST.SPRITES.BG.MENU_BG.MOUNTAIN_MOUNTAINS, "./assets/Sprites/bg/mountain-mountains.png")
        this.load.image(CST.SPRITES.UI.ICONS, "./assets/Sprites/ui/mac_os.png")
        this.load.image(CST.SPRITES.UI.START, "./assets/Sprites/ui/start.png")
        
        this.load.spritesheet(CST.SPRITES.UI.LAYER_2, "./assets/Sprites/ui/Layer-2.png", {
            frameHeight: 169.6,
            frameWidth: 82.17
        })
        this.load.spritesheet(CST.SPRITES.UI.LAYER_3, "./assets/Sprites/ui/Layer-3.png", {
            frameHeight: 169.6,
            frameWidth: 82.17
        })

        let loadingBar = this.add.graphics({
            fillStyle: {
                color: 0x32cd32
            }
        })

        this.load.on("progress", (percent) => {
            loadingBar.fillRect(0, this.game.renderer.height/2, this.game.renderer.width * percent, 80)
        })
    }

    create(){
        console.log("load");
        
        this.scene.start(CST.SCENES.MENU, "toster")
    }
}